import VContent from './components/Content'
import { cloudFunction } from '@/utils/cloud'

export default {
  components: { VContent },
  data () {
    return {
      contents: []
    }
  },
  onLoad (option) {
    this.getArticleDetail(option.title)
  },
  methods: {
    getArticleDetail (title) {
      cloudFunction('getArticle', { title })
        .then(res => {
          let [err, data] = res
          if (data.code === 0) {
            this.contents = data.data
          }
        }).catch(console.error)
    }
  }
}
